function Section({ title, items, setHue }: { title: string, items: any[], setHue: (h: number) => void }) {
  const [activeIndex, setActiveIndex] = useState(0);
  const activeItem = items[activeIndex];

  return (
    <section className="group">
      <div className="flex items-baseline gap-4 mb-8 border-b border-white/5 pb-4">
        <h2 className="text-xs font-bold tracking-[0.2em] uppercase text-white/30">{title}</h2>
      </div>

      {/* SELECTOR ROW */}
      <div className="flex flex-wrap gap-x-8 gap-y-2 mb-12">
        {items.map((item, idx) => (
          <button
            key={item.id}
            onClick={() => {
              setActiveIndex(idx);
              setHue(item.hue);
            }}
            className={`text-2xl md:text-3xl font-medium tracking-tight transition-all duration-300 ${
              idx === activeIndex 
              ? "text-white opacity-100 scale-100" 
              : "text-white/20 hover:text-white/50 scale-95"
            }`}
          >
            {item.company || item.title}
          </button>
        ))}
      </div>

      {/* CARD */}
      <div className="relative h-[320px] md:h-[280px]">
        <AnimatePresence mode="wait">
          <motion.div
            key={activeItem.id}
            initial={{ opacity: 0, y: 10, filter: "blur(4px)" }}
            animate={{ opacity: 1, y: 0, filter: "blur(0px)" }}
            exit={{ opacity: 0, y: -10, filter: "blur(4px)" }}
            transition={ANIM_TRANSITION}
            className="absolute inset-0 w-full"
          >
            <div className="grid grid-cols-1 md:grid-cols-[240px_1fr] gap-8 md:gap-16">
              
              {/* Image / Thumbnail */}
              <div className="hidden md:block w-full h-32 md:h-40 rounded-xl bg-gradient-to-br from-white/10 to-transparent border border-white/5 relative overflow-hidden">
                <div className={`absolute inset-0 opacity-20 bg-${activeItem.accent.split('-')[1]}-500`} />
              </div>

              {/* Text Content */}
              <div className="space-y-4">
                <div className="flex flex-col md:flex-row md:items-baseline md:justify-between gap-2">
                  <h3 className="text-3xl font-bold text-white">
                    {activeItem.company || activeItem.title}
                  </h3>
                  <span className={`text-sm font-mono uppercase tracking-wider ${activeItem.accent}`}>
                    {activeItem.role || activeItem.tech}
                  </span>
                </div>
                
                <p className="text-lg leading-relaxed text-white/70 max-w-2xl font-light">
                  {activeItem.desc}
                </p>

                {activeItem.date && (
                  <div className="text-sm text-white/30 font-mono mt-2">
                    {activeItem.date}
                  </div>
                )}
              </div>
            </div>
          </motion.div>
        </AnimatePresence>
      </div>
    </section>
  );
}

