'use client';

import { motion } from 'framer-motion';

interface AuroraProps {
  hue: number;
}

export default function AuroraBackground({ hue }: AuroraProps) {
  return (
    <div className="fixed inset-0 z-0 overflow-hidden bg-[#0a0a0a] pointer-events-none">
      {/* Master Hue Rotator 
         We rotate the entire container's filter. This changes the color of all orbs smoothly.
      */}
      <motion.div
        className="absolute inset-0"
        animate={{ filter: `hue-rotate(${hue}deg)` }}
        transition={{ duration: 1.5, ease: "easeInOut" }}
      >
        {/* ORB 1: Top Left - The "Main" Light */}
        <motion.div
          animate={{
            scale: [1, 1.2, 1],
            x: ["-10%", "10%", "-10%"],
            y: ["-10%", "10%", "-10%"],
          }}
          transition={{
            duration: 15,
            repeat: Infinity,
            repeatType: "mirror",
            ease: "easeInOut",
          }}
          className="absolute -top-[20%] -left-[20%] w-[70vw] h-[70vw] rounded-full bg-blue-700/30 blur-[120px] mix-blend-screen"
        />

        {/* ORB 2: Bottom Right - The "Deep" Shadow */}
        <motion.div
          animate={{
            scale: [1, 1.5, 1],
            x: ["10%", "-20%", "10%"],
            y: ["10%", "-20%", "10%"],
          }}
          transition={{
            duration: 20,
            repeat: Infinity,
            repeatType: "mirror",
            ease: "easeInOut",
          }}
          className="absolute -bottom-[20%] -right-[20%] w-[80vw] h-[80vw] rounded-full bg-purple-900/40 blur-[120px] mix-blend-screen"
        />

        {/* ORB 3: The "Wandering" Highlight - Creates the molten feel */}
        <motion.div
          animate={{
            x: ["-20%", "80%", "-20%"],
            y: ["20%", "50%", "20%"],
            rotate: [0, 180, 360],
          }}
          transition={{
            duration: 25,
            repeat: Infinity,
            repeatType: "mirror",
            ease: "linear",
          }}
          className="absolute top-[20%] left-[10%] w-[40vw] h-[40vw] rounded-full bg-emerald-900/20 blur-[100px] mix-blend-overlay"
        />
      </motion.div>

      {/* Vignette Overlay to force focus to center */}
      <div className="absolute inset-0 bg-gradient-to-b from-transparent via-[#0a0a0a]/50 to-[#0a0a0a]" />
    </div>
  );
}